import React, { useState, useEffect } from "react";
import { v4 as uuidv4 } from "uuid";
import dayjs from "dayjs";
import {
  PieChart,
  Pie,
  Cell,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

const COLORS = ["#00C49F", "#FF4444", "#FFD700"];

const incomeCategories = [
  { label: " Salary", value: "salary" },
  { label: " Gift", value: "gift" },
  { label: " Investment", value: "investment" },
  { label: " Rent", value: "rent" },
];

const expenseCategories = [
  { label: " Food", value: "food" },
  { label: " Rent", value: "rent" },
  { label: " Transport", value: "transport" },
  { label: " Entertainment", value: "entertainment" },
  { label: " Shopping", value: "shopping" },
];

function App() {
  const [transactions, setTransactions] = useState([]);
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState("");
  const [type, setType] = useState("income");
  const [category, setCategory] = useState("");
  const [date, setDate] = useState(dayjs().format("YYYY-MM-DD"));
  const [filterMonth, setFilterMonth] = useState(dayjs().format("YYYY-MM"));

  useEffect(() => {
    const data = localStorage.getItem("transactions");
    if (data) {
      setTransactions(JSON.parse(data));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem("transactions", JSON.stringify(transactions));
  }, [transactions]);

  const addTransaction = () => {
    if (!description || !amount || !date || !type || !category) return;

    const signedAmount =
      type === "income"
        ? Math.abs(parseFloat(amount))
        : type === "expense"
        ? -Math.abs(parseFloat(amount))
        : 0;

    const newTransaction = {
      id: uuidv4(),
      text: description,
      amount: signedAmount,
      date,
      type,
      category,
    };

    setTransactions([newTransaction, ...transactions]);
    setDescription("");
    setAmount("");
    setCategory("");
    setDate(dayjs().format("YYYY-MM-DD"));
  };

  const deleteTransaction = (id) => {
    setTransactions(transactions.filter((t) => t.id !== id));
  };

  const filtered = transactions.filter((t) =>
    t.date.startsWith(filterMonth)
  );

  const income = filtered
    .filter((t) => t.type === "income")
    .reduce((acc, t) => acc + t.amount, 0);

  const expenses = filtered
    .filter((t) => t.type === "expense")
    .reduce((acc, t) => acc + t.amount, 0);

  const savings = filtered
    .filter((t) => t.type === "savings")
    .reduce((acc, t) => acc + t.amount, 0);

  const chartData = [
    { name: "Income", value: income },
    { name: "Expenses", value: Math.abs(expenses) },
    { name: "Savings", value: savings },
  ];

  const getCategoryOptions = () => {
    return type === "income"
      ? incomeCategories
      : type === "expense"
      ? expenseCategories
      : [];
  };

  return (
    <div className="min-h-screen bg-gray-900 text-white p-4">
      <link href="https://fonts.googleapis.com/css2?family=Noto+Color+Emoji" rel="stylesheet" />
      <div className="max-w-2xl mx-auto bg-gray-800 p-6 rounded-xl shadow-xl font-sans">
        <h1 className="text-2xl font-bold mb-4 text-center"> Budget Tracker</h1>

        <div className="grid grid-cols-3 gap-4 text-center mb-6">
          <div>
            <h2 className="text-green-400">Income</h2>
            <p className="text-lg font-bold">${income.toFixed(2)}</p>
          </div>
          <div>
            <h2 className="text-red-400">Expenses</h2>
            <p className="text-lg font-bold">${Math.abs(expenses).toFixed(2)}</p>
          </div>
          <div>
            <h2 className="text-yellow-400">Savings</h2>
            <p className="text-lg font-bold">${savings.toFixed(2)}</p>
          </div>
        </div>

        <div className="mb-4">
          <input
            className="w-full p-2 mb-2 rounded bg-gray-700 text-white"
            placeholder="Description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
          <input
            className="w-full p-2 mb-2 rounded bg-gray-700 text-white"
            type="number"
            placeholder="Amount"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
          />
          <select
            className="w-full p-2 mb-2 rounded bg-gray-700 text-white"
            value={type}
            onChange={(e) => setType(e.target.value)}
          >
            <option value="income">Income</option>
            <option value="expense">Expense</option>
            <option value="savings">Savings</option>
          </select>
          {type !== "savings" && (
            <select
              className="w-full p-2 mb-2 rounded bg-gray-700 text-white"
              value={category}
              onChange={(e) => setCategory(e.target.value)}
            >
              <option value="">Select Category</option>
              {getCategoryOptions().map((cat) => (
                <option key={cat.value} value={cat.label}>
                  {cat.label}
                </option>
              ))}
            </select>
          )}
          <input
            className="w-full p-2 mb-2 rounded bg-gray-700 text-white"
            type="date"
            value={date}
            onChange={(e) => setDate(e.target.value)}
          />
          <button
            onClick={addTransaction}
            className="w-full bg-blue-600 hover:bg-blue-700 p-2 rounded"
          >
            Add Transaction
          </button>
        </div>

        <div className="mb-4">
          <label className="block mb-1">Filter by Month:</label>
          <input
            type="month"
            value={filterMonth}
            onChange={(e) => setFilterMonth(e.target.value)}
            className="w-full p-2 rounded bg-gray-700 text-white"
          />
        </div>

        <ul>
          {filtered.map((t) => (
            <li
              key={t.id}
              className={`flex justify-between items-center p-2 my-2 rounded ${
                t.type === "income"
                  ? "bg-green-700"
                  : t.type === "expense"
                  ? "bg-red-700"
                  : "bg-yellow-700"
              }`}
            >
              <div>
                <p className="font-semibold">{t.text}</p>
                <p className="text-sm text-gray-300">{t.category}</p>
                <p className="text-sm text-gray-400">{dayjs(t.date).format("MMM D, YYYY")}</p>
              </div>
              <div className="flex items-center gap-2">
                <span>${t.amount.toFixed(2)}</span>
                <button
                  onClick={() => deleteTransaction(t.id)}
                  className="text-white hover:text-gray-300"
                >
                  &times;
                </button>
              </div>
            </li>
          ))}
        </ul>

        <div className="mt-6">
          <h2 className="text-center font-semibold mb-2"> Financial Overview</h2>
          <div className="h-64">
            <ResponsiveContainer>
              <PieChart>
                <Pie
                  data={chartData}
                  dataKey="value"
                  nameKey="name"
                  cx="50%"
                  cy="50%"
                  outerRadius={80}
                  label
                >
                  {chartData.map((entry, index) => (
                    <Cell key={index} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
